<template>
  <div class="team-node">
    <!-- Team Header -->
    <div class="team-header d-flex align-center justify-space-between">
      <div>
        <strong>{{ team.teamName }}</strong>
      </div>
      <v-btn
        icon
        @click.stop="openEditor"
        v-if="canEditTeam"
      >
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
      <v-btn icon @click="toggleExpanded">
        <v-icon>{{ isExpanded ? 'mdi-chevron-down' : 'mdi-chevron-right' }}</v-icon>
      </v-btn>
    </div>

    <!-- Team Content -->
    <div v-if="isExpanded" class="team-content">
      <!-- Sub-teams -->
      <div v-if="team.teams.length">
        <h4>Sub-teams:</h4>
        <div class="sub-team">
          <TeamNode
            v-for="subteamId in team.teams"
            :key="subteamId"
            :team="teams[subteamId]"
            :teams="teams"
            :loginUser="loginUser"
            @open-editor="$emit('open-editor', $event)"
            @add-subteam="$emit('add-subteam', $event)"
          />
        </div>
      </div>
      <!-- Add Sub-team Button -->
      <v-btn
        v-if="canEditTeam"
        color="primary"
        text
        @click="addSubteam"
      >
        Add Sub-team
      </v-btn>
      <!-- Edit Button -->
    </div>
  </div>
</template>

<script>
export default {
  name: "TeamNode",
  props: {
    team: {
      type: Object,
      required: true,
    },
    teams: {
      type: Object,
      required: true,
    },
    loginUser: {
      type: Object,
      required: true,
    }
  },
  data() {
    return {
      isExpanded: false, // Tracks whether the team is expanded
    };
  },
  computed: {
    canEditTeam() {
      const userPermissions = this.team.userPermissions.find(
        (user) => user.userId === this.loginUser.userId // Replace with the current user ID
      );
      return userPermissions?.permissionLevel === "manager";
    },
  },
  methods: {
    toggleExpanded() {
      this.isExpanded = !this.isExpanded;
    },
    openEditor() {
      this.$emit("open-editor", this.team);
    },
    addSubteam() {
      this.$emit("add-subteam", this.team);
    },
  },
};
</script>

<style scoped>
.team-node {
  margin: 8px 0;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}
.team-header {
  padding: 8px;
  cursor: pointer;
  background-color: #e0e0e0;
  border-radius: 8px;
}
.team-content {
  margin-left: 16px;
  padding: 8px 0;
}
.sub-team {
  margin-left: 16px;
}
</style>

