<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title>
            <h2>User</h2>
          </v-card-title>
          <v-card-text>
            <h3>Username: {{this.loginUser.username}}</h3>
            <h3>User email: {{this.loginUser.userId}}</h3>
            <v-text-field v-model="newPassword" label="New Password" :append-inner-icon="'mdi-send'" @click:append-inner="changePassword" type="password"></v-text-field>

            <v-btn
              color="primary"
              @click="$emit('update:networkRequests','logout',null)"
              >
              Log out
            </v-btn>
          </v-card-text>
        </v-card>
        <v-card>
          <v-card-title>
            <h2>Teams</h2>
            <v-btn
              color="primary"
              @click="addTeam"
              >
              Create Team
            </v-btn>

          </v-card-title>
          <v-card-text>
            <!-- Root Teams -->
            <v-expansion-panels focusable multiple>
              <TeamNode
                v-for="team in rootTeams"
                :key="team.teamId"
                :team="team"
                :teams="teams"
                :loginUser="loginUser"
                @open-editor="openTeamEditor"
                @add-subteam="addSubteam"
              />
            </v-expansion-panels>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <!-- Team Editor -->
    <TeamEditor
      v-if="selectedTeam"
      :team="selectedTeam"
      :teams="teams"
      :networkRequests="networkRequests"
      @close="selectedTeam = null"
      @update-team="updateTeam"
      @update:networkRequests='pushNetworkRequests'
    />
  </v-container>
</template>

<script>
import TeamEditor from "./TeamEditor.vue";
import TeamNode from "./TeamNode.vue";

export default {
  name: "SettingsEdit",
  components: {
    TeamEditor,
    TeamNode,
  },
  props: {
    teams: {
      type: Object,
      required: true,
    },
    networkRequests: {
      type: Array,
      required: true,
    },
    loginUser: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      selectedTeam: null, // Team being edited
      newPassword: '',
    };
  },
  computed: {
    rootTeams() {
      return Object.values(this.teams).filter((team) =>
        !Object.values(this.teams).some((t) => t.teams.includes(team.teamId))
      );
    },
  },
  methods: {
    changePassword(){
      this.$emit('update:networkRequests','change-password', {"password": this.newPassword});
    },
    openTeamEditor(team) {
      this.selectedTeam = team;
    },
    addSubteam(parentTeam) {
      this.$emit("update:networkRequests","add-subteam",parentTeam)

    },
    addTeam() {
      this.$emit("update:networkRequests","add-team")

    },
    updateTeam(updatedTeam) {
      this.$set(this.teams, updatedTeam.teamId, updatedTeam);
    },
    pushNetworkRequests(params,params2=null,params3=null){
      this.$emit("update:networkRequests",params,params2,params3);
    }
  },
};
</script>
