<template>
  <v-dialog v-model="show" max-width="800">
    <v-card>
      <v-card-title>
        <h3>Edit Team: {{ currentTeam.teamName }}</h3>
      </v-card-title>
      <v-card-text>

        <!-- Team Name -->
        <v-text-field v-model="currentTeam.teamName" label="Team Name" dense outlined />
          <v-text-field 
            v-model="currentTeam.teamCode" 
            :append-inner-icon="'mdi-content-copy'"
            label="Team Code" 
            type="text"
            variant="filled"
            @click:append-inner="copyText(currentTeam.teamCode)"
          ></v-text-field>

        <!-- Team Users -->
        <h4>Users</h4>
        <v-list>
          <v-list-item v-for="user in currentTeam.userPermissions" :key="user.userId">
            <v-list-item-content>
              {{ user.userId }}
            </v-list-item-content>
            <v-select
              v-model="user.permissionLevel"
              :items="permissionLevels"
              label="Permission Level"
              dense
            ></v-select>
          </v-list-item>

        </v-list>
        <v-btn color="primary" text @click="addUser">Add User</v-btn>
        <v-text-field
              v-model="currentUser"
              label="Invite by email adress"
        >
        </v-text-field>

        <!-- Authorized Teams -->
        <h4>Allow Receive From Teams</h4>
        <v-list>
          <v-list-item
            v-for="teamId in currentTeam.allowedReceiveFromTeams"
            :key="teamId"
          >
            <v-list-item-content>
              {{ getTeamName(teamId) }}
            </v-list-item-content>
          </v-list-item>
        </v-list>

        <h4>Allow Send To Teams</h4>
        <v-list>
          <v-list-item
            v-for="teamId in currentTeam.allowedSendToTeams"
            :key="teamId"
          >
            <v-list-item-content>
              {{ getTeamName(teamId) }}
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <v-btn color="primary" text @click="addSendToTeam">
          Add Team
        </v-btn>
        <v-text-field
              v-model="currentSendToTeam"
              label="Add by team code"
        >
        </v-text-field>
      </v-card-text>
      <v-card-actions>
        <v-btn text color="secondary" @click="close">Close</v-btn>
        <v-btn text color="primary" @click="save">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>


<script>
export default {
  name: "TeamEditor",
  props: {
    team: {
      type: Object,
      required: true,
    },
    teams: {
      type: Object,
      required: true,
    },
    networkRequests: {
      type: Array,
      required: true,
    }
  },
  data() {
    return {
      show: true,
      permissionLevels: ["manager", "reviewer", "member"],
      currentUser: "",
      currentSendToTeam: "",
      currentReceiveFromTream: "",
    };
  },
  computed: {
    currentTeam(){
      return this.teams[this.team.teamId];
    }
  },
  methods: {
    getTeamName(teamId) {
      const team = this.teams[teamId];
      return team ? team.teamName : "Unknown Team";
    },
    addUser() {
      this.$emit("update:networkRequests",'add-user',{"teamId": this.team.teamId,"userId": this.currentUser})
      this.currentUser = '';
    },
    addSendToTeam() {
      this.$emit("update:networkRequests",'add-send-to-team',{"teamId": this.team.teamId,"sendToTeam": this.currentSendToTeam})
      console.log("ADD AUTHORIZED", this.team.teamName);
      //this.team.authorizedteams.push("");
    },
    addReceiveFromTeam() {
      this.$emit("update:networkRequests",'add-receive-from-team',{"teamId": this.team.teamId,"receiveFromTeam": this.currentReceiveFromTeam})
      console.log("ADD AUTHORIZED", this.team.teamName);
      //this.team.authorizedteams.push("");
    },
    close() {
      this.$emit("close");
    },
    save() {
      this.$emit("update:networkRequests", "update-team", this.team);
      this.close();
    },
    copyText(){
      navigator.clipboard.writeText(this.currentTeam.teamCode);
    }
  },
};
</script>


