<template>
  <div>
    <!-- Modal Overlay -->
    <div v-if="shouldDisplayCard" class="modal-overlay">
      <div class="modal-card">
        <v-card class="mx-auto" max-width="800" style="overflow-y: hidden; max-height: 90vh;">
          <v-card-title>
            <v-row justify="space-between" align="center">
              <!-- Task Creation Date -->
              <v-col class="text-right">
                <small>
                  Created: {{ formatDate(currentTask.createdAt) }}
                </small>
              </v-col>
              <!-- Task Title -->
              <v-col>
                <h3>{{currentTask.title}}</h3>
              </v-col>
              <v-col>
                <v-card-actions>
                  <v-btn color="red" text @click="statusBack" v-if="currentTask.status == 'In Progress'">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-stop-circle-outline" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Stop task</v-tooltip>
                  </v-btn>
                  <v-btn color="red" text @click="statusBack" v-if="currentTask.status == 'Done' && currentTask.type == 'Review'">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-undo-variant" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Restart task</v-tooltip>
                  </v-btn>
                  <v-btn color="green" text @click="statusForward" v-if="currentTask.status == 'To Do'">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-play-circle-outline" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Start task</v-tooltip>
                  </v-btn>
                  <v-btn color="green" text @click="statusForward" v-if="currentTask.status == 'In Progress'">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-check-circle-outline" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Complete task</v-tooltip>
                  </v-btn>
                  <v-btn v-if="isSubtask" color="red" text @click="forfeitTask">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-trash-can-outline" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Forfeit task</v-tooltip>
                  </v-btn>
                  <v-btn color="primary" text @click="saveAndClose" v-if="isEditingTask">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-content-save-edit-outline" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Save task</v-tooltip>
                  </v-btn>
                  <v-btn color="primary" text @click="closeCard">
                    <v-icon
                      :color="primary"
                    >
                      {{ "mdi-close" }}
                    </v-icon>
                    <v-tooltip
                      activator="parent"
                      location="top"
                    >Close without saving</v-tooltip>
                  </v-btn>
                </v-card-actions>
              </v-col>
            </v-row>
          </v-card-title>

          <v-card max-width="800" style="overflow-y: scroll; max-height: 70vh;">
            <v-card-text>
              <v-container>
                <v-row>
                  <!-- Progress Bar -->
                  <v-col  cols="12">
                    <v-progress-linear
                      v-model="currentTask.progressCalculated"
                      height="20"
                      color="green"
                    >
                      <template v-slot:default>
                        {{ currentTask.progressCalculated }}% completed
                      </template>
                    </v-progress-linear>
                    <v-slider
                      v-if="isSubtask"
                      v-model="currentTask.progressCalculated"
                      :min="0"
                      :max="100"
                      step="1"
                      label="Progress"
                    ></v-slider>
                  </v-col>

                  <!-- Title -->
                  <v-col cols="12" sm="6">
                    <v-text-field
                      v-model="currentTask.title"
                      label="Task Title"
                    ></v-text-field>
                  </v-col>

                  <!-- Team -->
                  <v-col cols="12" sm="6">
                    <v-select
                      :disabled="(currentTask.team || !isAdmin(currentTask.team))"
                      v-model="currentTask.team"
                      :items="teamOptions"
                      label="Team"
                    ></v-select>
                  </v-col>

                  <!-- Priority -->
                  <v-col cols="12" sm="6">
                    <v-select
                      :disabled="isSubtask"
                      v-model="currentTask.priority"
                      :items="priorityOptions"
                      label="Priority"
                    ></v-select>
                  </v-col>

                  <!-- Status -->
                  <v-col cols="12" sm="6">
                    <v-select
                      v-model="getStatus"
                      label="Status"
                      :disabled="true"
                    ></v-select>
                  </v-col>

                  <!-- Estimated Hours -->
                  <v-col cols="12" sm="6">
                    <v-text-field
                      :disabled="!isSubtask"
                      v-model.number="currentTask.estimatedHours"
                      label="Estimated Hours"
                      type="number"
                    ></v-text-field>
                  </v-col>

                  <!-- Actual Hours -->
                  <v-col v-if="!isSubtask && ((currentTask.createdBy == loginUser.userId) || (!currentTask.delegatedFrom && isAdmin(currentTask.team)))" cols="12" sm="6">
                    <v-text-field
                      v-model.number="currentTask.actualHours"
                      label="Predicted hours"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  
                  <!-- Deadline -->
                  <v-col cols="12" sm="6">
                    <label for="date-time-picker">Deadline</label>
                    <VueDatepicker
                      v-model="selectedDeadline"
                      format="dd-MM-yyyy HH:mm"
                      :flow="['year','month','calendar','time']"
                      :partial-flow="true"
                      :year-first="true"
                      :enable-time="true"
                      :start-time="{hours: 0, minutes: 0}"
                      :auto-apply="true"
                      :use-24-hour="true"
                      :locale="'en-US'"
                      id="date-time-picker"
                      input-class="custom-input"
                    ></VueDatepicker>
                  </v-col>

                  <!-- Value Generated -->
                  <v-col v-if="isAdmin(currentTask.team) && !currentTask.parent" cols="12" sm="6">
                    <v-text-field
                      v-model.number="currentTask.valueGenerated"
                      label="Value Generated ($)"
                      type="number"
                    ></v-text-field>
                  </v-col>

                  <!-- Assigned To -->
                  <v-col v-if="isAdmin(currentTask.team)" cols="12">
                    <v-select
                      v-model="selectedUsernames"
                      :items="users.map(user => user.username)"
                      :disabled="!isSubtask"
                      label="Assigned To"
                      chips
                      @change="updateAssignedTo"
                    ></v-select>
                  </v-col>

                  <!-- Description -->
                  <v-col cols="12">
                    <v-textarea
                      v-model="currentTask.description"
                      label="Description"
                      rows="4"
                    ></v-textarea>
                  </v-col>

                  <!-- Tags -->
                  <v-col cols="12">
                    <v-combobox
                      v-model="currentTask.tags"
                      :items="availableTags"
                      label="Tags"
                      multiple
                      chips
                    ></v-combobox>
                  </v-col>

                </v-row>

                <!-- Subtasks Section -->
                <v-row v-if="currentTask.type != 'Unassigned'">
                  <v-col cols="12">
                    <h3>Subtasks</h3>
                    <v-list v-if="currentTask.subtasks.length && true">
                      <v-list-item
                        v-for="subtaskId in currentTask.subtasks"
                        :key="subtaskId"
                      >
                        <div>
                          <span>{{ currentSubtasks.find(subtask => subtask.taskId== subtaskId).title }}</span>
                          <v-progress-linear
                            v-model="currentSubtasks.find(subtask=>subtask.taskId== subtaskId).progressCalculated"
                            height="5"
                            color="green"
                          >
                          </v-progress-linear>

                        </div>
                        <v-btn
                          text
                          small
                          color="primary"
                          @click="viewSubtask(subtaskId)"
                        >
                          View
                        </v-btn>
                      </v-list-item>
                    </v-list>
                    <v-btn
                      color="primary"
                      text
                      @click="addSubtask"
                    >
                      Add Subtask
                    </v-btn>
                  </v-col>
                </v-row>

                <!-- Comments Section -->
                <v-row>
                  <v-col cols="12">
                    <h3>Comments</h3>
                    <v-list>
                      <v-list-item
                        v-for="comment in currentTask.comments"
                        :key="comment.commentId"
                      >
                        <div>
                          <p>
                            <strong>{{ getUser(comment.author).username }}</strong>
                            <span>({{ formatDate(comment.createdAt) }})</span>
                          </p>
                          <p>{{ comment.content }}</p>
                        </div>
                      </v-list-item>
                    </v-list>
                    <!-- Add New Comment -->
                    <v-textarea
                      v-model="newComment"
                      label="Add a comment"
                    ></v-textarea>
                    <v-btn
                      color="primary"
                      text
                      @click="addComment"
                    >
                      Add Comment
                    </v-btn>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col cols="12">
                    <v-row v-if="currentTask.status=='Done' && currentTask.type == 'Assigned'">
                      <h3>Review</h3>
                        <v-icon
                          v-for="index in 5"
                          :key="index"
                          :color="index <= currentTask.valuation.completionRating ? 'yellow' : 'gray'"
                          class="star-icon"
                        >
                          mdi-star
                        </v-icon>

                    </v-row>
                    <v-row v-if="currentTask.status=='Done' && currentTask.type == 'Assigned'" class="stars">
                          <v-text-field
                            v-model="currentTask.valuation.feedback"
                            label="Feedback"
                          ></v-text-field>
                    </v-row>
                    <v-row v-if="currentTask.status=='Done' && currentTask.type != 'Assigned'" class="stars">
                        <span>Reviewing...</span>
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TaskEdit",
  props: {
    taskList: {
      type: Array,
      required: true,
    },
    isEditingTask: {
      type: [String, null],
      default: null,
    },
    users: {
      type: Array,
      required: true,
    },
    loginUser: {
      type: Object,
      required: true,
    },
    teams: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      availableTags: [], // Add available tags dynamically or statically
      statusOptions: ["Pending Approval", "To Do", "In Progress", "Done"],
      priorityOptions: ["High","Medium","Low","None"],
      deadlinePickerOrder: ['year','month','calendar','time'],
      currentTask: {},
      selectedUsernames: [], // Stores selected usernames
      newComment: "",
      selectedDeadline: null,
    };
  },
  computed: {
    isSubtask(){
      if(this.currentTask)
        return (this.currentTask.subtasks.length == 0)
      else 
        return 0
    },
    shouldDisplayCard() {
      return this.isEditingTask !== null;
    },
    getStatus() {
      if(this.currentTask.type=="Review")
        return "Reviewing";
      else
        return this.currentTask.status;
    },
    teamOptions() {
      let teamList = [];
      let subteamList = [];
      for (let team of this.loginUser.teams){
        if(this.isAdmin(team)){
          teamList.push(team);
          if(this.teams[team].allowedSendToTeams)
            subteamList.push(...this.teams[team].allowedSendToTeams);
        }
      }
      for (let team of subteamList){
        if(teamList.indexOf(team) == -1)
          teamList.push(team);
      }
      return teamList;
    },
    currentSubtasks() {
      let subtasks = this.taskList.filter(task=> (this.currentTask.subtasks.indexOf(task.taskId) > -1));
      return subtasks;
    }
  },
  watch: {
    isEditingTask: {
      immediate: true,
      handler(newVal) {
        if (newVal !== null) {
          this.setCurrentTask(newVal);
        }
      },
    },
    // Synchronize `selectedUsernames` with the current `assignedTo` user IDs
    currentTask: {
      immediate: true,
      deep: true,
      handler(newVal) {
        if (newVal && Array.isArray(newVal.assignedTo)) {
          this.selectedUsernames = newVal.assignedTo
            .map((userId) => this.getUser(userId)?.username || "Unknown User")
            .filter((username) => username);
        } else {
          this.selectedUsernames = [];
        }
      },
    },
  },
  methods: {
    setCurrentTask(taskId) {
      let defaultTask = {
        title: "New Task",
        description: "",
        type: "Assigned",
        team: this.loginUser.teams[0],
        status: "Pending Approval",
        parent: null,
        deadline: null,
        priority: "None",
        estimatedHours: 0,
        actualHours: null,
        valueGenerated: null,
        assignedTo: [this.loginUser.userId],
        tags: [],
        progressCalculated: 0,
        subtasks: [],
        comments: [],
        createdBy: this.loginUser.userId,
        createdAt: Date.now(),
        valuation: {"completionRating": null, "feedback": null},
        auction: {},
      };
      defaultTask.taskId = this.loginUser.username + (new Date()).toString();
      const task = this.taskList.find((task) => task.taskId === taskId) || defaultTask;
      this.selectedDeadline = task.deadline;

      this.currentTask = structuredClone(task);
    },
    updateAssignedTo() {
      // Map selected usernames to their corresponding user IDs
      if (!this.selectedUsernames || !this.users) return;

      this.currentTask.assignedTo = this.selectedUsernames
        .map((username) =>
          this.users.find((user) => user.username === username)?.userId
        )
        .filter((id) => id); // Exclude null or undefined IDs
    },
    saveTask() {
      this.updateAssignedTo();
      if(this.selectedDeadline)
        this.currentTask.deadline = (new Date(this.selectedDeadline)).toISOString();
      this.$emit("update:networkRequests", 'edit-task' , this.currentTask);
    },
    forfeitTask() {
      this.currentTask.forfeitText = "forfeited task";
      this.$emit("update:networkRequests", "task-forfeit", this.currentTask);
      this.closeCard()
    },
    closeCard() {
      if(this.currentTask.parent)
        this.$emit("update:isEditingTask", this.currentTask.parent);
      else
        this.$emit("update:isEditingTask", null);

      this.currentTask = {
        title: "",
        description: "",
        status: "",
        progressCalculated: 0,
        subtasks: [],
        comments: [],
      };
    },
    saveAndClose() {
        this.saveTask();
        this.closeCard();
    },
    addSubtask() {
      let newTaskId = this.loginUser.userId + Date.now().toString();
      let defaultSubtask = {
        taskId: newTaskId, // Generate new ID
        title: `New Subtask ${newTaskId%20}`,
        description: "",
        type: "Assigned",
        team: this.currentTask.team,
        status: "Pending Approval",
        parent: this.currentTask.taskId,
        deadline: this.currentTask.deadline,
        priority: this.currentTask.priority,
        estimatedHours: 0,
        actualHours: null,
        valueGenerated: null,
        assignedTo: this.currentTask.assignedTo,
        tags: [],
        progressCalculated: 0,
        subtasks: [],
        comments: [],
        createdBy: this.loginUser.userId,
        createdAt: Date.now(),
        valuation: {"completionRating": null, "feedback": null},
        auction: {},
      };

      if(this.currentTask.assignedTo.length){
        defaultSubtask.type = "Assigned";
      }
      
      this.$emit("update:networkRequests", "add-subtask", defaultSubtask);

      this.$emit("update:isEditingTask", newTaskId);
    },
    viewSubtask(taskId) {
      this.$emit("update:isEditingTask", taskId);
    },
    addComment() {
      if (this.newComment.trim()) {
        this.currentTask.comments.push({
          commentId: Date.now().toString(),
          author: this.loginUser.userId, // Example author ID
          content: this.newComment.trim(),
          createdAt: new Date().toISOString(),
        });
        this.newComment = "";
      }
    },
    getUser(userId) {
      return this.users.find((user) => user.userId === userId) || {
        username: "Unknown User",
      };
    },
    formatDate(dateString) {
      if (!dateString) return "N/A";
      const date = new Date(dateString);
      return date.toLocaleString();
    },
    statusBack() {
      switch(this.currentTask.status) {
        case "Pending Approval":
          this.currentTask.status = null;
          this.currentTask.type="Unassigned";
          break;
        case "To Do":
          this.currentTask.status = "Pending Approval";
          break;
        case "In Progress":
          if(this.currentTask.type=="Review")
            this.currentTask.type="Assigned"
          else
            this.currentTask.status = "To Do";
          break;
        case "Done":
          this.currentTask.type="Assigned";
          this.currentTask.status = "In Progress";
          break;
      }
    },
    statusForward() {
      switch(this.currentTask.status) {
        case "Pending Approval":
          this.currentTask.status = "To Do";
          break;
        case "To Do":
          this.currentTask.status = "In Progress";
          break;
        case "In Progress":
          this.currentTask.type="Review";
          this.currentTask.progressCalculated=100;
          this.currentTask.status = "Done";
          break;
      }
    },
    isAdmin(team) {
      if(!team)
        return false
      const user = this.teams[team].userPermissions.find((user) => user.userId === this.loginUser.userId);
      return (user.permissionLevel === "manager");
    },
  },
};
</script>



<style scoped>
/* Modal-like overlay wrapper */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5); /* Dim background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000; /* Ensure it appears above other elements */
}

/* Card wrapper */
.modal-card {
  max-width: 600px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 16px;
}
.custom-input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}
</style>

