<template>
  <v-container class="login-container" fluid>
    <v-row justify="center">
      <v-col cols="12" md="4">
        <v-card class="login-card">
          <v-card-title class="text-h5 text-center">
            Login
          </v-card-title>
          <v-card-text>
            <v-form ref="loginForm" v-model="valid">
              <!-- Username Field -->
              <v-text-field
                v-model="form.username"
                label="Username"
                :rules="[rules.required]"
                clearable
                outlined
                dense
              ></v-text-field>

              <!-- Password Field -->
              <v-text-field
                v-model="form.password"
                label="Password"
                :rules="[rules.required]"
                type="password"
                clearable
                outlined
                dense
              ></v-text-field>

              <!-- Error Message -->
              <v-alert
                v-if="errorMessage"
                type="error"
                class="mt-3"
                dismissible
              >
                {{ errorMessage }}
              </v-alert>

              <!-- Submit Button -->
              <v-btn
                color="primary"
                block
                class="mt-3"
                @click="submitLogin"
                :disabled="!valid"
              >
                Login
              </v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

