### **Script Section**

```javascript
<script>
export default {
  name: "LoginForm",
  props: {
    loginUser: {
      type: Object,
      required: true,
    },
    currentPage: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
      valid: true, // Tracks form validity
      errorMessage: "", // Holds error messages
      rules: {
        required: (value) => !!value || "This field is required",
      },
    };
  },
  methods: {
    async submitLogin() {
      this.$emit("update:networkRequests", 'login', this.form);

      this.errorMessage = ""; // Clear previous errors

      //if (!this.$refs.loginForm.validate()) {
        //return;
      //}

    },
  },
};
</script>
