import { createApp } from 'vue';
import App from './App.vue';

// Import Vuetify and styles
import { createVuetify } from 'vuetify';
import 'vuetify/styles';
import * as components from 'vuetify/components'; // Import all Vuetify components
import * as directives from 'vuetify/directives'; // Import Vuetify directives
import VueDatepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import '@mdi/font/css/materialdesignicons.css';

// Create Vuetify instance with selected components and directives
const vuetify = createVuetify({
    components,
    directives,
});

// Create and mount Vue app with Vuetify
createApp(App)
  .directive('drop', {
    beforeMount(el, binding) {
      // Placeholder: Add custom drop handling logic if necessary
      el.addEventListener('drop', binding.value);
    },
    unmounted(el) {
      el.removeEventListener('drop', el.dropHandler);
    }
  })
  .use(vuetify)
  .component('VueDatepicker', VueDatepicker)
  .mount('#app');


