<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6">
        <v-card class="pa-3">
          <v-card-title class="text-h6">
            Value Generated by User
          </v-card-title>
          <v-card-text>
            <canvas id="userValueChart"></canvas>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" md="6">
        <v-card class="pa-3">
          <v-card-title class="text-h6">
            Value Generated by Task Status
          </v-card-title>
          <v-card-text>
            <canvas id="statusValueChart"></canvas>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { Chart, registerables } from "chart.js";
Chart.register(...registerables);

export default {
  name: "TaskStatistics",
  props: {
    tasks: {
      type: Array,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      userValueData: {},
      statusValueData: {},
    };
  },
  mounted() {
    this.calculateData();
    this.renderUserValueChart();
    this.renderStatusValueChart();
  },
  watch: {
    currentTask: {
      immediate: true,
      deep: true,
      handler(newVal) {
        if (newVal) {
          this.calculateData();
          this.renderUserValueChart();
          this.renderStatusValueChart();
        }
      },
    }
  },
  methods: {
    calculateData() {
      // Initialize data structures
      this.userValueData = {};
      this.statusValueData = {};

      // Iterate through tasks and calculate value generated
      this.tasks.forEach((task) => {
        const valueGenerated =
          (task.valueGenerated * (task.progressCalculated || 0)) / 100;

        // Aggregate by user
        task.assignedTo.forEach((userId) => {
          const user = this.users.find((u) => u.userId === userId);
          const username = user ? user.username : "Unknown User";

          if (!this.userValueData[username]) {
            this.userValueData[username] = 0;
          }
          this.userValueData[username] += valueGenerated;
        });

        // Aggregate by status
        const status = task.status || "Unknown";
        if (!this.statusValueData[status]) {
          this.statusValueData[status] = 0;
        }
        this.statusValueData[status] += valueGenerated;
      });
    },
    renderUserValueChart() {
      const ctx = document.getElementById("userValueChart").getContext("2d");
      new Chart(ctx, {
        type: "bar",
        data: {
          labels: Object.keys(this.userValueData),
          datasets: [
            {
              label: "Value Generated ($)",
              data: Object.values(this.userValueData),
              backgroundColor: "rgba(54, 162, 235, 0.6)",
              borderColor: "rgba(54, 162, 235, 1)",
              borderWidth: 1,
            },
          ],
        },
        options: {
          responsive: true,
          plugins: {
            legend: { display: false },
            tooltip: { enabled: true },
          },
          scales: {
            x: { title: { display: true, text: "Users" } },
            y: { title: { display: true, text: "Value Generated ($)" } },
          },
        },
      });
    },
    renderStatusValueChart() {
      const ctx = document.getElementById("statusValueChart").getContext("2d");
      new Chart(ctx, {
        type: "pie",
        data: {
          labels: Object.keys(this.statusValueData),
          datasets: [
            {
              label: "Value Generated ($)",
              data: Object.values(this.statusValueData),
              backgroundColor: [
                "rgba(255, 99, 132, 0.6)",
                "rgba(54, 162, 235, 0.6)",
                "rgba(255, 206, 86, 0.6)",
                "rgba(75, 192, 192, 0.6)",
                "rgba(153, 102, 255, 0.6)",
              ],
              borderColor: [
                "rgba(255, 99, 132, 1)",
                "rgba(54, 162, 235, 1)",
                "rgba(255, 206, 86, 1)",
                "rgba(75, 192, 192, 1)",
                "rgba(153, 102, 255, 1)",
              ],
              borderWidth: 1,
            },
          ],
        },
        options: {
          responsive: true,
          plugins: {
            legend: { position: "top" },
            tooltip: { enabled: true },
          },
        },
      });
    },
  },
};
</script>
<style scoped>
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.v-card {
  padding: 16px;
  height: auto;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

#userValueChart,
#statusValueChart {
  max-width: 100%;
  height: 300px;
}
</style>

