<template>
  <v-container >
    <!-- Unassigned Tasks List -->
    <v-row>
      <v-col cols="12">
        <v-list dense>
          <v-list-item
            v-for="task of unassignedTasks"
            :key="task.taskId"
            class="task-item"
          >
            <!-- Task Summary -->
            <v-list-item-content @click="$emit('update:isEditingTask',task.taskId)">
              <v-list-item-title>
                <strong>{{ task.title }}</strong>
                <v-chip>{{ getTeamName(task.team) }}</v-chip>
              </v-list-item-title>
              <v-list-item-subtitle>
                <strong>Deadline:</strong> {{ formatDate(task.deadline) }}
                <v-chip
                  :color="getPriorityColor(task.priority)"
                  class="priority-chip"
                  small
                >
                  {{ task.priority }}
                </v-chip>
              </v-list-item-subtitle>
            </v-list-item-content>
            <v-btn
              icon
              @click="toggleTask(task.taskId)"
            >
              <v-icon>{{ isTaskExpanded(task.taskId) ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>

            <!-- Expanded Task Details -->
            <v-expand-transition>
              <div v-show="isTaskExpanded(task.taskId)" class="task-details">

                <p v-if="task.delegatedFrom"><strong>Received From:</strong> {{ task.delegatedFrom }}</p>
                <p v-if="task.description"><strong>Description:</strong> {{ task.description }}</p>
                <p><strong>Estimated Hours Needed:</strong> {{ task.estimatedHours }} hours</p>
                <p><strong>Created By:</strong> {{ getUserName(task.createdBy) }}</p>
                <p v-if="task.valueGenerated"><strong>Value:</strong> ${{ task.valueGenerated }}</p>

                <!-- Comments Section -->
                <v-btn
                  text
                  small
                  color="primary"
                  @click="toggleComments(task.taskId)"
                >
                  {{ areCommentsExpanded(task.taskId) ? 'Hide Comments' : 'Show Comments' }}
                </v-btn>
                <v-expand-transition>
                  <div v-show="areCommentsExpanded(task.taskId)" class="comments-section">
                    <v-list dense>
                      <v-list-item
                        v-for="comment in task.comments"
                        :key="comment.commentId"
                      >
                        <v-list-item-content>
                          <p>
                            <strong>{{ getUserName(comment.author) }}</strong>:
                            {{ comment.content }}
                          </p>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list>
                  </div>
                </v-expand-transition>

                <!-- Task Action Buttons -->
                <v-card-actions>
                  <v-btn v-if="isAdmin(task.team)" color="success">
                    Assign
                    <v-menu activator="parent">
                      <v-list>
                        <v-list-item
                          v-for="user of teams[task.team].userPermissions"
                          :key="user.userId"
                          :value="user.userId"
                          @click="assignTask(task,user.userId)"
                        >
                          <v-list-item-title>{{ user.userId }}</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                  </v-btn>
                  <v-btn v-if="isAdmin(task.team)" color="success">
                    Auction
                    <v-menu 
                      activator="parent"
                      :close-on-content-click="false"
                      :location="bottom"
                      >

                      <v-list>
                      <v-list-item>
                        <v-card min-width="300" min-height="400" >
                          <label for="date-time-picker">Deadline</label>
                          <VueDatepicker
                            v-model="auctionDeadline"
                            format="dd-MM-yyyy HH:mm"
                            :flow="['year','month','calendar','time']"
                            :partial-flow="true"
                            :year-first="true"
                            :enable-time="true"
                            :start-time="{hours: 0, minutes: 0}"
                            :auto-apply="true"
                            :use-24-hour="true"
                            :locale="'en-US'"
                            id="date-time-picker"
                            input-class="custom-input"
                          ></VueDatepicker>
                          <v-btn
                            color="primary"
                            @click="auctionTask(task,auctionDeadline)"
                          >
                            Send
                          </v-btn>
                        </v-card>
                      </v-list-item>
                      </v-list>
                    </v-menu>
                  </v-btn>

                  <v-btn v-if="isAdmin(task.team)" color="info">
                    Transfer
                    <v-menu activator="parent">
                      <v-list>
                        <v-list-item
                          v-for="team of getTeams()"
                          :key="team"
                          :value="team"
                          @click="transferTask(task,team)"
                        >
                          <v-list-item-title>{{ getTeamName(team) }}</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                  </v-btn>
                </v-card-actions>
                <v-card-actions>
                  <v-btn @click="acceptTask(task)" color="success">
                    Accept
                  </v-btn>

                  
                  <v-btn color="error">
                    Forfeit
                    <v-menu activator="parent"
                      :close-on-content-click="false">

                      <v-list>
                        <v-list-item>
                          <v-textarea
                            min-width="300"
                            v-model="forfeitText"
                            label="Reason for forfeiting the task"
                            >
                          </v-textarea>
                        </v-list-item>
                        <v-list-item>
                            <v-btn
                              color="primary"
                              @click="forfeitTask(task,forfeitText)"
                            >
                              Send
                            </v-btn>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                  </v-btn>
                </v-card-actions>
              </div>
            </v-expand-transition>
          </v-list-item>
        </v-list>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "TaskAssign",
  props: {
    tasks: {
      type: Array,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
    teams: {
      type: Object,
      required: true,
    },
    loginUser: {
      type: Object,
      required: true,
    }
  },
  data() {
    return {
      expandedTasks: {}, // Tracks expanded tasks
      expandedComments: {}, // Tracks expanded comments for each task
      forfeitText: "",
      auctionDeadline: "",
    };
  },
  computed: {
    unassignedTasks() {
      return this.tasks.filter((task) => (task.subtasks.length==0) && ((task.status == "Pending Approval" && task.type == "Assigned" && (task.assignedTo.indexOf(this.loginUser.userId)!= -1))  || (task.type === "Unassigned" && this.teams[task.team].userPermissions.find((user)=>user.userId==this.loginUser.userId).permissionLevel == "manager")));
    },
  },
  methods: {
    acceptTask(task){
      let newTask = structuredClone(task);
      this.$emit("update:networkRequests",'task-accept',newTask);
    },
    assignTask(task,userId){
      let newTask = structuredClone(task);
      newTask.assignedTo = [userId];
      this.$emit("update:networkRequests",'task-assign',newTask);
    },
    auctionTask(task,auctionDeadline){
      let newTask = structuredClone(task);
      newTask.auction = {};
      newTask.auction.deadline = auctionDeadline.toISOString();
      this.$emit("update:networkRequests", 'task-auction',newTask);
    },
    transferTask(task,teamId){
      //task.transferTeam = teamId; TODO optimize team transfer
      this.$emit("update:networkRequests", 'task-transfer', {"task": task, "teamId": teamId});
    },
    forfeitTask(task,forfeitText){
      let newTask = structuredClone(task);
      newTask.forfeitText = forfeitText;
      this.$emit("update:networkRequests", 'task-forfeit', newTask);
    },
    getUserName(userId) {
      const user = this.users.find((u) => u.userId === userId);
      return (user ? user.username : "Unknown User");
    },
    getTeamName(teamId) {
      const team = Object.values(this.teams).find((team) => team.teamId === teamId);
      return (team ? team.teamName: "Unknown Team");
    },
    isAdmin(team) {
      if(!team)
        return false
      const user = this.teams[team].userPermissions.find((user) => user.userId === this.loginUser.userId);
      return (user.permissionLevel === "manager");
    },
    getTeams() {
      let teamList = [];
      let subteamList = [];
      for (let team of this.loginUser.teams){
        if(this.isAdmin(team)){
          teamList.push(team);
          if(this.teams[team].allowedSendToTeams)
            subteamList.push(...this.teams[team].allowedSendToTeams);
        }
      }
      for (let team of subteamList){
        if(teamList.indexOf(team) == -1)
          teamList.push(team);
      }
      return teamList;
    },
    getPriorityColor(priority) {
      const colors = {
        High: "red",
        Medium: "orange",
        Low: "green",
        None: "gray",
      };
      return colors[priority] || "gray";
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString(undefined, {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
      });
    },
    toggleTask(taskId) {
      this.expandedTasks[taskId] = !this.expandedTasks[taskId];
    },
    isTaskExpanded(taskId) {
      return !!this.expandedTasks[taskId];
    },
    toggleComments(taskId) {
      this.expandedComments[taskId] = !this.expandedComments[taskId];
    },
    areCommentsExpanded(taskId) {
      return !!this.expandedComments[taskId];
    },
  },
};
</script>

<style scoped>
.task-item {
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 10px;
}

.task-details {
  padding: 10px;
  background: #f9f9f9;
  border-radius: 5px;
}

.comments-section {
  padding: 10px;
  background: #f0f0f0;
  border-radius: 5px;
  margin-top: 10px;
}

.priority-chip {
  font-weight: bold;
  color: white;
}
</style>

