<template>
  <v-container>
    <!-- Auctioned Tasks List -->
    <v-row>
      <v-col cols="12" md="6" class="mx-auto">
        <v-card class="pa-3">
          <v-card-title>
            <span class="text-h6">Auctioned Tasks</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-list dense>
              <v-list-item
                v-for="task in filteredTasks"
                :key="task.taskId"
                @click="selectTask(task)"
                class="task-item"
              >
                <div>
                  <v-list-item-title class="text-primary">
                    {{ task.title }}
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    <strong>Estimated:</strong> {{ task.estimatedHours }}h
                  </v-list-item-subtitle>
                  <v-list-item-subtitle>
                    <strong>Min Bid:</strong> {{ getMinimumBid(task) }}h
                  </v-list-item-subtitle>
                  <v-list-item-subtitle>
                    <strong>Ends In:</strong> {{ getRemainingTime(task) }}
                  </v-list-item-subtitle>
                </div>
                <v-btn icon>
                  <v-icon color="primary">mdi-chevron-right</v-icon>
                </v-btn>
              </v-list-item>
            </v-list>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <!-- Selected Task Bids -->
    <v-row v-if="selectedTask" justify="center" class="mt-5">
      <v-col cols="12" md="8">
        <v-card class="pa-3">
          <v-card-title>
            <span class="text-h6">Bids for Task: {{ selectedTask.title }}</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <!-- Bids List -->
            <v-list dense>
              <v-list-item
                v-for="bid in sortedBids"
                :key="bid.userId"
                class="bid-item"
              >
                <div>
                  <v-list-item-title>
                    <strong>{{ getUserName(bid.userId) }}</strong>
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    <strong>Bid Hours:</strong> {{ bid.bidHours }} hours
                  </v-list-item-subtitle>
                </div>
              </v-list-item>
            </v-list>

            <!-- Add New Bid -->
            <v-divider class="my-4"></v-divider>
            <h4 class="text-h6">Place Your Bid</h4>
            <v-form ref="bidForm" v-model="bidFormValid">
              <v-text-field
                v-model.number="newBidHours"
                label="Enter Bid Hours"
                type="number"
                min="1"
                :rules="[rules.required, rules.positive]"
                dense
                outlined
                class="mb-3"
              ></v-text-field>
              <v-btn
                color="primary"
                block
                @click="placeBid"
              >
                Place Bid
              </v-btn>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn text color="secondary" @click="deselectTask">
              Back to Task List
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "TaskAuction",
  props: {
    tasks: {
      type: Array,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
    loginUser: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      selectedTask: null, // Task currently being viewed
      newBidHours: null, // New bid hours input
      bidFormValid: false, // Validation state for the bid form
      rules: {
        required: (value) => !!value || "This field is required",
        positive: (value) => value > 0 || "Value must be greater than 0",
      },
    };
  },
  mounted(){
    this.updateAuction();
  },
  computed: {
    filteredTasks() {
      return this.tasks.filter(
        (task) => task.type === "Auctioned" && !task.auction.isAuctioned
      );
    },
    sortedBids() {
      if (!this.selectedTask || !this.selectedTask.auction) {
        return [];
      }
      return [...this.selectedTask.auction.bids].sort(
        (a, b) => a.bidHours - b.bidHours
      );
    },
  },
  methods: {
    selectTask(task) {
      this.selectedTask = task;
    },
    deselectTask() {
      this.selectedTask = null;
      this.newBidHours = null;
    },
    getMinimumBid(task) {
      if (!task.auction || task.auction.bids.length === 0) {
        return "No bids";
      }
      return Math.min(...task.auction.bids.map((bid) => bid.bidHours));
    },
    getRemainingTime(task) {
      const endDate = new Date(task.auction.deadline);
      const now = new Date();

      const diff = endDate - now;
      if (diff <= 0) {
        return "Expired";
      }

      const days = Math.floor(diff / (1000*60*60*24));
      const hours = Math.floor(diff % (1000 * 60 * 60*24)/(1000*60*60));
      const minutes = Math.floor((diff % (1000 * 60 * 60) / (1000*60)));
      return `${days}d ${hours}h ${minutes}m`;
    },
    getUserName(userId) {
      const user = this.users.find((u) => u.userId === userId);
      return user ? user.username : "Unknown User";
    },
    placeBid() {
      if (!this.bidFormValid || !this.selectedTask) return;

      let data = {
        task: this.selectedTask,
        bid: {
            userId: this.loginUser.userId, // Replace with actual user ID
            bidHours: this.newBidHours,
        },
      };

      this.$emit("update:networkRequests", 'place-bid', data);

      this.newBidHours = null;
    },
    updateAuction(){
      let currentDate = new Date;
      currentDate = currentDate.toISOString();
      for(let task of this.tasks){
        console.log("Updating Tasks",task.title, currentDate, task.auction.deadline);
        if(currentDate > task.auction.deadline){
          this.$emit("update:networkRequests", 'task-auction-update', task);
        }
      }
    }
  },
};
</script>

<style scoped>
.task-item {
  cursor: pointer;
  padding: 8px 0;
  border-bottom: 1px solid #e0e0e0;
}

.task-item:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.bid-item {
  padding: 8px 0;
}

.bid-item:not(:last-child) {
  border-bottom: 1px solid #e0e0e0;
}

.text-primary {
  color: #1976d2;
}
</style>


