<template>
  <v-container fluid >
    <v-row>
      <!-- Kanban Columns -->
      <v-col
        v-for="(column, columnIndex) in columns"
        :key="columnIndex"
        class="kanban-column"
        cols="12"
        md="4"
        @dragover.prevent="onDragOver"
        @drop="onDrop(column.status)"
      >
        <v-card>
          <v-card-title>{{ column.name }}</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <!-- Task Cards -->
            <div
              v-for="task in getTasksByStatus(column.status)"
              :key="task.taskId"
              class="kanban-task"
              draggable="true"
              @dragstart="() => setDraggedTask(task)"
              @click="() => emitEditTask(task.taskId)"
            >
              <!-- Task Header with Title and Priority -->
              <div class="task-header">
                <!-- Task Title -->
                <div class="task-title-wrapper">
                    <span class="task-title">
                        {{ task.title }}
                    </span>
                </div>

                <!-- Priority Icon -->
                <div class="priority-icon-wrapper">
                  <v-icon
                    :color="getPriorityColor(task.priority)"
                    class="priority-icon"
                  >
                    {{ getPriorityIcon(task.priority) }}
                  </v-icon>
                </div>
              </div>

              <!-- Progress Bar -->
              <v-progress-linear
                v-model="task.progressCalculated"
                height="5"
                color="green"
                class="my-2"
              ></v-progress-linear>

              <!-- Deadline -->
              <p class="task-deadline">
                <strong>Deadline:</strong> {{ formatDate(task.deadline) }}
              </p>

              <!-- Assigned Users -->
              <div class="task-users">
                <v-chip
                >
                {{getTeam(task.team)}}
                </v-chip>
                <div
                  v-for="userId in task.assignedTo.slice(0, 3)"
                  :key="userId"
                  class="user-avatar"
                  :style="{ backgroundColor: getUserColor(userId) }"
                  :title="getUser(userId).username"
                >
                  <span>
                    {{ getUser(userId).picture ? '' : getUserInitial(getUser(userId).username) }}
                  </span>
                </div>
                <div
                  v-if="task.assignedTo.length > 3"
                  class="user-avatar more-users"
                  title="More users"
                >
                  +{{ task.assignedTo.length - 3 }}
                </div>
              </div>
              <div v-if="column.name == 'Done' && task.type == 'Assigned'" class="stars">
                    <v-icon
                      v-for="index in 5"
                      :key="index"
                      :color="index <= task.valuation.completionRating ? 'yellow' : 'gray'"
                      @click="rateTask(task, index)"
                      class="star-icon"
                    >
                      mdi-star
                    </v-icon>

              </div>
              <div v-if="column.name == 'Done' && task.type != 'Assigned'" class="stars">
                  <span>Reviewing...</span>

              </div>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "KanbanBoard",
  props: {
    taskList: {
      type: Array,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
    teams: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      columns: [
        { name: "To Do", status: "To Do" },
        { name: "In Progress", status: "In Progress" },
        { name: "Done", status: "Done" },
      ],
      draggedTask: null, // Temporarily holds the dragged task
    };
  },
  methods: {
    /**
     * Get tasks filtered by their status.
     * @param {String} status - The status of the tasks to filter.
     * @returns {Array} Filtered list of tasks.
     */
    getTasksByStatus(status) {
      return this.taskList.filter((task) => task.status === status);
    },

    /**
     * Set the dragged task during drag start.
     * @param {Object} task - The task being dragged.
     */
    setDraggedTask(task) {
      this.draggedTask = task;
    },

    /**
     * Handle the drag over event to prevent default behavior.
     */
    onDragOver(event) {
      event.preventDefault();
    },

    /**
     * Handle the drop event to move a task to a new column.
     * @param {String} status - The target status for the dropped task.
     */
    onDrop(status) {
      if (this.draggedTask) {
        if(status == "Done" && this.draggedTask.status != "Done"){
          this.draggedTask.type = "Review";
          this.draggedTask.progressCalculated = 100;
        } else if (status != "Done" && this.draggedTask.status == "Done"){
          this.draggedTask.type = "Assigned";
        }
        this.draggedTask.status = status; // Update the task's status
        this.$emit("update:networkRequests", 'edit-task' , this.draggedTask);
        //this.$emit("update:task", this.draggedTask); // Notify parent of the updated list
        this.draggedTask = null; // Clear the dragged task
      }
    },

    /**
     * Emit an event to edit the selected task.
     * @param {String} taskId - ID of the selected task.
     */
    emitEditTask(taskId) {
      this.$emit("update:isEditingTask", taskId); // Notify parent that the task is being edited
    },

    /**
     * Get the color associated with a priority level.
     * @param {String} priority - The priority level.
     * @returns {String} The associated color.
     */
    getPriorityColor(priority) {
      switch (priority) {
        case "High":
          return "red";
        case "Medium":
          return "orange";
        case "Low":
          return "green";
        default:
          return "gray";
      }
    },

    /**
     * Get the icon associated with a priority level.
     * @param {String} priority - The priority level.
     * @returns {String} The associated icon.
     */
    getPriorityIcon(priority) {
      switch (priority) {
        case "High":
          return "mdi-alert-circle"; // Represents urgency/high priority
        case "Medium":
          return "mdi-alert"; // Represents caution/medium priority
        case "Low":
          return "mdi-alert"; // Represents completion/low priority
        default:
          return null; // Default for unknown priority
      }
    },

    /**
     * Format a date into a human-readable string.
     * @param {String} date - The date to format.
     * @returns {String} Formatted date string.
     */
    formatDate(date) {
      if (!date) return "No deadline";
      const options = { year: "numeric", month: "short", day: "numeric" };
      return new Date(date).toLocaleDateString(undefined, options);
    },

    /**
     * Get user information by userId.
     * @param {String} userId - The ID of the user to retrieve.
     * @returns {Object} User object or a default object if not found.
     */
    getUser(userId) {
      let defaultUser = {
        username: "Unknown",
      };
      if(!this.users)
        return defaultUser;

      return this.users.find((user) => user.userId === userId) || defaultUser;
    },
    getTeam(teamId) {
      return this.teams[teamId].teamName || "Unknown";
    },
    /**
     * Get the initial of a user's username.
     * @param {String} username - The username of the user.
     * @returns {String} The initial of the username.
     */
    getUserInitial(username) {
      return username ? username.charAt(0).toUpperCase() : "?";
    },

    /**
     * Get a consistent color for a user based on their ID.
     * @param {String} userId - The user ID.
     * @returns {String} A hex color code.
     */
    getUserColor(userId) {
      const colors = ["#FFB6C1", "#ADD8E6", "#90EE90", "#FFD700", "#DDA0DD"];
      const index = userId.charCodeAt(0) % colors.length;
      return colors[index];
    },
  },
};
</script>


