<template>
  <div>
    <!-- Foldable Menu Indicator -->
    <div
      class="menu-indicator"
      @click="toggleMenu"
      :class="{ 'menu-expanded': menuExpanded }"
    >
      <v-icon>{{ menuExpanded ? 'mdi-chevron-left' : 'mdi-filter' }}</v-icon>
    </div>

    <!-- Foldable Menu -->
    <v-card
      v-if="menuExpanded"
      class="menu-card"
      outlined
      elevation="2"
    >
      <v-card-title>
        <span class="text-h6">Filter Tasks</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <!-- Filters -->
        <v-container>
          <v-row>
            <!-- Management Filter -->
            <v-col v-if="Object.keys(teams).some(team => isAdmin(team))" cols="12">
              <v-checkbox
                v-model="filters.management"
                label="Management view"
                dense
              ></v-checkbox>
            </v-col>

            <!-- Expired Tasks Filter -->
            <v-col cols="12">
              <v-checkbox
                v-model="filters.expired"
                label="Show Expired Tasks"
                dense
              ></v-checkbox>
            </v-col>
            <!-- Team Filter -->
            <v-col cols="12">
              <v-select
                v-model="filters.team"
                :items="getUniqueValues('team')"
                label="Team"
                dense
                multiple
                clearable
                chips
              ></v-select>
            </v-col>

            <!-- Status Filter -->
            <v-col cols="12">
              <v-select
                v-model="filters.status"
                :items="getUniqueValues('status')"
                label="Status"
                dense
                multiple
                clearable
                chips
              ></v-select>
            </v-col>

            <!-- Priority Filter -->
            <v-col cols="12">
              <v-select
                v-model="filters.priority"
                :items="getUniqueValues('priority')"
                label="Priority"
                dense
                multiple
                clearable
                chips
              ></v-select>
            </v-col>

            <!-- Assigned To Filter -->
            <v-col cols="12">
              <v-select
                v-model="filters.assignedTo"
                :items="getUniqueValues('assignedTo', true)"
                label="Assigned To"
                dense
                multiple
                clearable
                chips
              ></v-select>
            </v-col>

            <!-- Tags Filter -->
            <v-col cols="12">
              <v-combobox
                v-model="filters.tags"
                :items="getUniqueValues('tags', true)"
                label="Tags"
                dense
                multiple
                clearable
                chips
              ></v-combobox>
            </v-col>

          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-btn text @click="resetFilters">Reset</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>
<script>
export default {
  name: "TaskFilter",
  props: {
    loginUser: {
      type: Object,
      required: true,
    },
    taskStorage: {
      type: Array,
      required: true,
    },
    teams: {
      type: Object,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      menuExpanded: false, // Controls menu visibility
      filters: {
        team: [],
        status: [],
        priority: [],
        assignedTo: [],
        tags: [],
        expired: false,
        management: false,
      },
    };
  },
  mounted() {
    this.applyFilters();
  },
  watch:{
    taskStorage: {
      deep: true,
      handler(newVal) {
        if(newVal !== null) {
          this.applyFilters();
        }
      }
    },
    filters: {
      deep: true,
      handler(newVal) {
        if(newVal !== null) {
          this.applyFilters()
        }
      }
    }
  },
  methods: {
    isAdmin(team) {
      if(!team)
        return false
      const user = this.teams[team].userPermissions.find((user) => user.userId === this.loginUser.userId);
      return (user.permissionLevel === "manager");
    },
    toggleMenu() {
      this.menuExpanded = !this.menuExpanded;
    },
    getUniqueValues(field, isArray = false) {
      if (!this.taskStorage || this.taskStorage.length === 0) return [];
      if (isArray) {
        const values = this.taskStorage.flatMap((task) =>
          Array.isArray(task[field]) ? task[field] : []
        );
        return [...new Set(values)];
      } else {
        return [...new Set(this.taskStorage.map((task) => 
          /*
          {
          if(field == "team"){
            return this.getTeam(task[field]) || null;
          } else if(field == "assignedTo"){
            return this.getUser(task[field]) || null;
          } else {
          }
          }
          */
            task[field] || null
        ))];
      }
    },
    getTeam(teamId){
      return this.teams[teamId].teamName  || "Unknown";
    },
    getUser(userId) {
      return this.users.find((user) => user.userId === userId) || "Unknown";
    },
    applyFilters() {
      let filteredTasks = [...this.taskStorage];
        
      // Filter by expired tasks
      if (this.filters.management) {
        filteredTasks = filteredTasks.filter(
          (task) => (!task.parent || task.subtasks.length != 0) 
        );
      } else {
        filteredTasks = filteredTasks.filter(
          (task) => (task.subtasks.length == 0 && task.assignedTo.includes(this.loginUser.userId)) 
        );
      }

      filteredTasks = filteredTasks.filter((task) =>
        {return task.type != "Auctioned"}
      );

      // Filter by team
      if (this.filters.team.length > 0) {
        filteredTasks = filteredTasks.filter((task) =>
          this.filters.team.includes(task.team)
        );
      }

      // Filter by status
      if (this.filters.status.length > 0) {
        filteredTasks = filteredTasks.filter((task) =>
          this.filters.status.includes(task.status)
        );
      }

      // Filter by priority
      if (this.filters.priority.length > 0) {
        filteredTasks = filteredTasks.filter((task) =>
          this.filters.priority.includes(task.priority)
        );
      }

      // Filter by assignedTo
      if (this.filters.assignedTo.length > 0) {
        filteredTasks = filteredTasks.filter((task) =>
          task.assignedTo.some((user) =>
            this.filters.assignedTo.includes(user)
          )
        );
      }

      // Filter by tags
      if (this.filters.tags.length > 0) {
        filteredTasks = filteredTasks.filter((task) =>
          task.tags.some((tag) => this.filters.tags.includes(tag))
        );
      }

      // Filter by expired tasks
      if (this.filters.expired) {
        const now = new Date();
        filteredTasks = filteredTasks.filter(
          (task) => new Date(task.deadline) < now
        );
      }

      // Sort by deadline and priority
      filteredTasks.sort((a, b) => {
        const deadlineA = new Date(a.deadline || 0).getTime();
        const deadlineB = new Date(b.deadline || 0).getTime();
        const priorityMap = { High: 1, Medium: 2, Low: 3, None: 4 };

        if (deadlineA !== deadlineB) {
          return deadlineA - deadlineB; // Sort by earliest deadline
        }

        return (
          (priorityMap[a.priority] || 5) - (priorityMap[b.priority] || 5) // Sort by priority
        );
      });

      this.$emit("update:taskList", filteredTasks); // Emit the filtered and sorted list
    },
    resetFilters() {
      this.filters = {
        status: [],
        priority: [],
        assignedTo: [],
        tags: [],
        expired: false,
      };
      this.applyFilters(); // Reset the task list to the original order
    },
  },
};
</script>
<style scoped>
.menu-indicator {
  position: fixed;
  top: 100px;
  left: 0;
  background-color: #1976d2;
  color: white;
  cursor: pointer;
  padding: 10px;
  border-radius: 0 8px 8px 0;
  z-index: 1500;
}

.menu-indicator.menu-expanded {
  left: 250px;
  border-radius: 8px 0 0 8px;
}

.menu-card {
  position: fixed;
  top: 100px;
  left: 0;
  width: 250px;
  height: calc(100vh - 120px);
  z-index: 1400;
  overflow-y: auto;
}
</style>

