<template>
  <v-app>
    <NavBar
      :taskList="taskList"
      :loginUser="loginUser"
      @update:currentPage="(data) => currentPage = data"
      @create-new-task="handleCreateNewTask"
    />
    <NetworkHandler
      :networkRequests="networkRequests"
      @update:loginUser="(data) => loginUser = data"
      @update:teamData="(data) => teamData = data"
      @update:taskStorage="(data) => taskStorage = data"
      @update:teams="(data) => teams = data"
      @update:users="(data) => users = data"
      @update:currentPage="(data) => currentPage = data"
      @update:networkRequests="pushNetworkRequests"
      @clearNetworkRequests="() => networkRequests = []"
    />
    
    <button id="install-button" style="display: none;">Install App</button>
  <v-main>
    <v-container style="max-width:100vw; min-width:90vw;" >
      <div class="main-layout" >
        <TaskFilter
          v-if="taskStorage && (isCurrentPage('main') || isCurrentPage('inbox') || isCurrentPage('auction') || isCurrentPage('reportsT'))"
          :taskStorage="taskStorage" 
          :users="users"
          :teams="teams"
          :loginUser="loginUser"
          @update:taskList="updateTaskList"
        />
        <KanbanBoard 
          v-if="isCurrentPage('main') && taskList"
          :taskList="taskList" 
          :users="users"
          :teams="teams"
          @update:isEditingTask="updateIsEditingTask"
          @update:networkRequests="pushNetworkRequests"
        />
        <SettingsEdit
          v-if="isCurrentPage('teams')"
          :loginUser="loginUser"
          :networkRequests="networkRequests"
          :teams="teams" 
          @update:networkRequests="pushNetworkRequests"
        />
        <LoginForm
          v-if="isCurrentPage('login')"
          :loginUser="loginUser"
          :currentPage="currentPage"
          @update:networkRequests="pushNetworkRequests"
        />
        <StatisticsTeam
          v-if="isCurrentPage('reportsT')"
          :tasks="taskList" 
          :users="users"
          @update:networkRequests="pushNetworkRequests"
        />
        <TaskAuction
          v-if="isCurrentPage('auction')"
          :loginUser="loginUser"
          :tasks="taskStorage" 
          :users="users"
          @update:networkRequests="pushNetworkRequests"
        />
        <TaskInbox
          v-if="isCurrentPage('inbox')"
          :loginUser="loginUser"
          :tasks="taskStorage" 
          :users="users"
          :teams="teams"
          @update:isEditingTask="updateIsEditingTask"
          @update:networkRequests="pushNetworkRequests"
        />
      </div>

      <TaskEdit
        v-if="loginUser.userId && true"
        :taskList="taskStorage"
        :loginUser="loginUser"
        :users="users"
        :teams="teams"
        :isEditingTask="isEditingTask"
        @update:isEditingTask="updateIsEditingTask"
        @update:networkRequests="pushNetworkRequests"
      />

    </v-container>
    <AppFooter/>
  </v-main>
  </v-app>
</template>

<script>
import NavBar from './components/NavBar.vue';
import NetworkHandler from './components/NetworkHandler.vue';
import KanbanBoard from './components/KanbanBoard.vue';
import TaskFilter from './components/TaskFilter.vue';
import TaskEdit from './components/TaskEdit.vue';
import LoginForm from './components/LoginForm.vue';
import TaskInbox from './components/TaskInbox.vue';
import SettingsEdit from './components/SettingsEdit.vue';
import StatisticsTeam from './components/StatisticsTeam.vue';
import TaskAuction from './components/TaskAuction.vue';
import AppFooter from './components/AppFooter.vue';


export default {
  name: 'App',
  components: {
    NavBar,
    NetworkHandler,
    TaskFilter,
    KanbanBoard,
    TaskEdit,
    LoginForm,
    TaskInbox,
    SettingsEdit,
    StatisticsTeam,
    TaskAuction,
    AppFooter
  },
  data() {
    return {
      deferredPrompt: null,  // Store the beforeinstallprompt event,
      teamData: [],
      taskStorage: null,
      teams: null,
      users: null,
      taskList: null,
      isEditingTask: null,
      currentPage: 'login',
      loginUser: null,
      networkRequests: [],
      server_changeLog: [],
    };
  },
  mounted() {
    // Listen for the beforeinstallprompt event
    window.addEventListener('beforeinstallprompt', (e) => {
      e.preventDefault();
      this.deferredPrompt = e;
      const installButton = document.getElementById('install-button');
      if (installButton) {
        installButton.style.display = 'block';
      }
    });

    // Install button click listener
    const installButton = document.getElementById('install-button');
    if (installButton) {
      installButton.addEventListener('click', async () => {
        if (this.deferredPrompt) {
          this.deferredPrompt.prompt();
          const { outcome } = await this.deferredPrompt.userChoice;
          if (outcome === 'accepted') {
            console.log('User accepted the install prompt');
          } else {
            console.log('User dismissed the install prompt');
          }
          this.deferredPrompt = null;
          installButton.style.display = 'none';
        }
      });
    }
  },
  methods: {
    async fetchData() {
      const response = await fetch('https://jsonplaceholder.typicode.com/posts');
      this.data = await response.json();
    },
    pushNetworkRequests(action,data=null) {
      this.networkRequests.push({"action": action, "data": data});
    },
    updateTaskList(updatedTasks) {
        this.taskList = updatedTasks;
    },
    handleCreateNewTask() {
      // Generate a new random task ID
      this.isEditingTask = " ";//Math.floor(Math.random() * 10000).toString();

      // TODO Placeholder for backend request to create a new task
      // When implementing the backend, replace this random ID generation
      // with a request to the server to create a new task and retrieve the new task ID.
    },
    updateIsEditingTask(updatedValue) {
        this.isEditingTask = updatedValue;
    },
    isCurrentPage (testValue) {
        return testValue == this.currentPage;
    },
  },
};
</script>

