    <template v-slot:prepend>
        <v-img
          @click="navigateTo('main')"
          src="/assets/logo.png"
          alt="App Logo"
          max-height="40"
          min-width="40"
          class="mr-3"
        ></v-img>
        <span class="text-h6">
          FYNIX.PRO
        </span>
    </template>
    <template v-slot:append>
      <v-btn
        v-if="loginUser.userId && true"
        color="success"
        class="mr-3"
