---

<script>
export default {
  name: "NavBar",
  props: {
    loginUser: {
      type: Object,
      default: null,
    }
  },
  data() {
    return {
      placeholderImage: "/assets/placeholder-avatar.png",
      menuItems: [
        { text: "Inbox", event: "inbox", icon: "mdi-inbox" },
        { text: "To-do", event: "main", icon: "mdi-check-all" },
        { text: "Auction", event: "auction", icon: "mdi-account-group" },
        //{ text: "Teams", event: "teams", icon: "mdi-account-group" },
        //{ text: "Review", event: "review", icon: "mdi-check-all" },
        { text: "Stats", event: "reportsT", icon: "mdi-chart-box" },
        //{ text: "Management Report", event: "reportsM", icon: "mdi-chart-box" },
        { text: "Settings", event: "teams", icon: "mdi-cog" },
      ],
    };
  },
  methods: {
    navigateTo(event) {
      this.$emit("update:currentPage", event);
    },
    emitCreateTaskEvent() {
      this.$emit("create-new-task");
    },
    toggleMenu() {
      this.$emit("update:toggleFoldableMenu",true);
    },
