<template>
<div>
</div>
</template>

<script>
export default {
  name: "NetworkHandler",
  props: {
    networkRequests: {
      type: Array,
      required: true,
    }
  },
  data() {
    return {
      loginUser: {},
      teamData: {},
      users: [],
      domain: 'fynix.pro',
    };
  },
  mounted() {
    this.getNetworkRequests();
    this.server_getChangeLog();
    this.$emit("update:networkRequests",'login',null);
    this.syncCycle();
  },
  watch: {
    networkRequests: {
      immediate: true,
      deep: true,
      handler(newVal) {
        if (newVal.length) {
          this.syncData();
        }
      }
    },
    teamData: {
      immediate: true,
      deep: true,
      handler(newVal) {
        if (newVal !== null) {
          this.$emit('update:teamData',newVal);
          this.$emit('update:taskStorage',this.taskStorage);
          this.$emit('update:teams',this.teams);
        }
      }
    },
    loginUser: {
      immediate: true,
      deep: true,
      handler(newVal) {
          this.$emit('update:loginUser',newVal)
      }
    },
  },
  computed: {
    taskStorage(){
      let taskStorage = [];
      for(let team in this.teamData){
        for(let task of this.teamData[team].taskStorage){
          task.team = team;
        }
        taskStorage.push(...this.teamData[team].taskStorage);
      }
      taskStorage = taskStorage.filter(task=>((this.teamData[task.team].userPermissions.find(user=>user.userId == this.loginUser.userId).permissionLevel=="manager") || task.assignedTo.some(userId=>userId == this.loginUser.userId)));
      return taskStorage;
    },
    teams(){
      let teamPermissions = {};
      for(let team in this.teamData){
          let data = this.teamData[team];
          teamPermissions[data.teamId] = {};
          let temp = teamPermissions[data.teamId];
          temp.teamName = data.teamName;
          temp.teamId = data.teamId;
          temp.teams = data.teams;
          temp.allowedSendToTeams = data.allowedSendToTeams;
          temp.allowedReceiveFromTeams = data.allowedReceiveFromTeams;
          temp.userPermissions = data.userPermissions;
          temp.teamCode = data.teamCode;
      }
      return teamPermissions;
    }
  },
  methods: {
    delay(time) {
      return new Promise(resolve => setTimeout(resolve, time));
    },
    async syncCycle() {
      await this.delay(10000);
      this.$emit("update:networkRequests",'get-team-data',null);
      this.syncCycle();
    },
    async syncData() {
      //TODO compare network requests and server history, and determine requests to be executed
      let requests = this.networkRequests;

      if(requests.length){
        console.log("Syncing ",requests.length);
        this.$emit("clearNetworkRequests");
        for(let request of requests){
          console.log(request.action,request.data);
          switch(request.action) {
            case('login'):
              await this.submitLogin(request.data);
              break;
            case('logout'):
              this.submitLogout(request.data);
              break;
            case('get-team-data'):
              this.getTeamData();
              break;
            case('edit-task'):
              await this.updateTask(request.data);
              break;
            case('place-bid'):
              await this.placeBid(request.data);
              break;
            case('add-user'):
              await this.addUser(request.data);
              break;
            case('change-password'):
              await this.changePassword(request.data);
              break;
            case('add-subteam'):
              await this.addTeam(request.data);
              break;
            case('add-team'):
              await this.addTeam();
              break;
            case('add-send-to-team'):
              await this.addSendToTeam(request.data);
              break;
            case('add-receive-from-team'):
              await this.addReceiveFromTeam(request.data);
              break;
            case('update-team'):
              await this.updateTeam(request.data);
              break;
            case('add-subtask'):
              await this.addSubtask(request.data);
              break;
            case('task-accept'):
              await this.taskAccept(request.data);
              break;
            case('task-assign'):
              await this.taskAssign(request.data);
              break;
            case('task-auction'):
              await this.taskAuction(request.data);
              break;
            case('task-review'):
              await this.taskReview(request.data);
              break;
            case('task-transfer'):
              await this.taskTransfer(request.data);
              break;
            case('task-forfeit'):
              await this.taskForfeit(request.data);
              break;
            case('task-auction-update'):
              await this.taskAuctionUpdate(request.data);
              break;
          }
        }
      }
      
      if(Object.keys(this.teamData).length)
       localStorage.setItem('teamData', JSON.stringify(this.teamData));
      //localStorage.setItem('networkRequests', JSON.stringify(this.networkRequests));
      return; 
    },
    async submitLogin(form){
      let body = {};
      if (form)
        body = form
      // Network request to /user/login
      try {
        const response = await fetch('/api/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(body),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }else {
          const data = await response.json();
          if(data.user){
            this.loginUser = data.user;
            console.log("Login successful", this.loginUser);
            this.$emit("update:networkRequests",'get-team-data',null);
            this.$emit('update:currentPage', 'main'); // Redirect to main page
          }
        }
      } catch (error) {
        this.errorMessage = error.message || "Login failed.";
        alert(this.errorMessage);
      }
    },
    async submitLogout(){
      // Network request to /user/logout
      try {
        const response = await fetch('/api/logout', {
          method: 'GET',
          headers: { 'Content-Type': 'application/json' },
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }else {
          this.loginUser = {};
          this.$emit('update:currentPage', 'login'); // Redirect to main page
        }
      } catch (error) {
        this.$emit('update:currentPage', 'login'); // Redirect to main page
        this.errorMessage = error.message || "Logout failed.";
        alert(this.errorMessage);
      }
    },
    server_getChangeLog() {
        let changeLog = JSON.parse(localStorage.getItem('changeLog') || 'null');
        if(changeLog)
          this.server_changeLog = changeLog;
        else
          this.server_changeLog = [];
    },
    async getUsers () {
        let userIds = [];
        for (let team of Object.values(this.teams)){
          for(let user of team.userPermissions){
            if(userIds.indexOf(user.userId)== -1){
              userIds.push(user.userId);
            }
          }
        }
        try {
          const response = await fetch('/api/get-users/', {
              headers: {
                "Content-Type": "application/json",
              },
              method: 'POST',
              credentials: 'include', // Ensures the session cookie is sent
              body: JSON.stringify(userIds)
          });

          if (!response.ok) {
            if(response?.status == 401)
              this.$emit("update:currentPage",'login');
            throw new Error('Unauthorized');
          }

          this.users = await response.json();
          this.$emit('update:users',this.users);
        } catch (error) {
          console.error('Error fetching protected data:', error);
        }
    },
    async getTeamData() {
      /*
      let userDataStored = JSON.parse(localStorage.getItem('teamData') || 'null');
      let loginUser = JSON.parse(localStorage.getItem('loginUser') || 'null');
      if(loginUser && this.loginUser.userId == loginUser.userId && userDataStored != null){
        console.log('Recovering state');
        this.teamData = userDataStored;
      }
      else{
      */
        console.log('Retrieving teamData');
        try {
          const response = await fetch('/api/get-team-data', {
              method: 'GET',
              credentials: 'include', // Ensures the session cookie is sent
          });

          if (!response.ok) {
            if(response?.status == 401)
              this.$emit("update:currentPage",'login');
            throw new Error('Unauthorized');
          }

          this.teamData = await response.json();
        } catch (error) {
          console.error('Error fetching protected data:', error);
        }
        await this.getUsers();
      /*
      }
      */
    },
    getNetworkRequests() {
        let storedRequests= JSON.parse(localStorage.getItem('networkRequests') || 'null');
        //let defaultRequest = {"action":'login',"data":null};
        //this.$emit("update:networkRequests", defaultRequest.action, defaultRequest.data);
        if(storedRequests)
          for(let request of storedRequests)
            this.$emit("update:networkRequests", request.action, request.data);
    },
    calculateValue(task,parentTask){
      task.generatedValue = parentTask.generatedValue * (parentTask.estimatedTimeHours/task.estimatedTimeHours);
      if(parentTask.state == "Done"){
        task.state = "Done";
      }
      this.updateTask(task);

      let subtasks = this.teamData[task.team].taskStorage.filter(task=> (task.subtasks.indexOf(task.taskId) > -1));
      if(subtasks.length != task.subtasks.length){
        console.log("Searching other teams for subtasks...");
        for(let team of Object.values(this.teamData)){
          let foundTasks = null;
          if(team.teamId != task.teamId)
            foundTasks = team.taskStorage.filter(task=> (task.subtasks.indexOf(task.taskId) > -1));
          if(foundTasks){
            subtasks.push(...foundTasks);
          }
          if(subtasks.length == task.subtasks.length)
            break;
        }
      }
      for (let subtask of subtasks)
        this.calculateValue(subtask,task);
    },
    calculateProgress(taskId,team){
      let parentTask = this.teamData[team].taskStorage.find(task => task.taskId == taskId);
      if(!parentTask){
        console.log("Searching other teams for parent...");
        for(let team of Object.values(this.teamData)){
          let foundTask = team.taskStorage.find(task => task.taskId == taskId);
          if(foundTask){
            parentTask = foundTask;
            break;
          }
        }
      }

      let subtasks = this.teamData[team].taskStorage.filter(task=> (parentTask.subtasks.indexOf(task.taskId) > -1));
      if(subtasks.length != parentTask.subtasks.length){
        console.log("Searching other teams for subtasks...");
        for(let team of Object.values(this.teamData)){
          let foundTasks = null;
          if(team.teamId != team)
            foundTasks = team.taskStorage.filter(task=> (parentTask.subtasks.indexOf(task.taskId) > -1));
          if(foundTasks){
            subtasks.push(...foundTasks);
          }
          if(subtasks.length == parentTask.subtasks.length)
            break;
        }
      }
      
      let totalTime = 0;
      let completedTime = 0;
      let assignedTo = [];
      for(let task of subtasks){
        totalTime = totalTime + task.estimatedHours;
        completedTime = completedTime + (task.estimatedHours * (task.progressCalculated/100))
        if(assignedTo.indexOf(task.assignedTo[0]) == -1)
          assignedTo.push(...task.assignedTo);
      }
      parentTask.estimatedHours = totalTime;
      if(totalTime != 0)
        parentTask.progressCalculated = Math.round(completedTime/totalTime*100);
      else
        parentTask.progressCalculated = 0;

      parentTask.assignedTo = assignedTo;
      this.updateTask(parentTask);

      if(parentTask.parent && true)
        this.calculateProgress(parentTask.parent,parentTask.team);
      else
        for(let task of subtasks)
          this.calculateValue(task);
      return
    },
    async updateTask(updatedTask) {
      try {
        const response = await fetch('/api/edit-task', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(updatedTask),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.$emit('update:currentPage', 'login'); // Redirect to main page
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }

      const previousTask = this.taskStorage.find(task => (task.taskId === updatedTask.taskId));

      if (previousTask) {
        const index = this.teamData[previousTask.team].taskStorage.findIndex(task => (task.taskId === updatedTask.taskId));
        if(previousTask.team == updatedTask.team){
          this.teamData[previousTask.team].taskStorage.splice(index, 1, updatedTask); // Replace with updated
        } else {
          this.taskTransfer({"task": updatedTask, "teamId": updatedTask.team});
        }
      } else {
        this.teamData[updatedTask.team].taskStorage.push(updatedTask);
      }

      if(updatedTask.parent && true)
        this.calculateProgress(updatedTask.parent,updatedTask.team);
    },
    async placeBid(data) {
      try {
        const response = await fetch('/api/place-bid', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
      let task = data.task;
      let bid = data.bid; 
      let previousTask = this.taskStorage.find(taskT => (taskT.taskId === task.taskId));
      previousTask.auction.bids.push(bid);
    },
    async addUser(data) {
      this.teamData[data.teamId].userPermissions.push({"userId": data.userId, "permissionLevel": "member"});
      try {
        const response = await fetch('/api/add-user', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
    },
    async changePassword(data) {
      try {
        const response = await fetch('/api/change-password', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }

      this.teamData[data.teamId].userPermissions.push({"userId": data.userId});
    },
    async addTeam(parentTeam = null){
      let newTeam = {
        teamId: "",
        teamName: "New Team",
        teamCode: "",
        teams: [],
        parent: "",
        taskStorage: [],
        userPermissions: [{"userId": this.loginUser.userId, "permissionLevel": "manager"}],
        authorizedteams: [],
      };
      newTeam.teamId = this.loginUser.userId + Date.now().toString(); // Generate a unique ID
      newTeam.teamCode = this.loginUser.userId + Date.now().toString(); // Generate a unique ID
      if(parentTeam){
        newTeam.parent = parentTeam.teamId;
        this.teamData[parentTeam.teamId].teams.push(newTeam.teamId);
      }
      this.teamData[newTeam.teamId] = newTeam;
      this.users.find((userT) => userT.userId == this.loginUser.userId).teams.push(newTeam.teamId);

      try {
        const response = await fetch('/api/add-team', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(newTeam),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
    },
    async addSendToTeam(data) {
      try {
        const response = await fetch('/api/add-send-to-team', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
      let foundTeam = null;
      for(let value of Object.values(this.teamData))
        if (value.teamCode == data.sendToTeam){
          foundTeam = value;
          break;
        }

      if(foundTeam != null){
        this.teamData[data.teamId].allowedSendToTeams.push(foundTeam.teamId);
        this.teamData[foundTeam.teamId].allowedReceiveFromTeams.push(data.teamId);
      }
      else
        console.log("Team not found");
    },
    async addReceiveFromTeam(data) {
      try {
        const response = await fetch('/api/add-receive-from-team', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
      let foundTeam = null;
      for(let value of Object.values(this.teamData))
        if (value.teamCode == data.receiveFromTeam){
          foundTeam = value;
        }

      if(foundTeam != null)
        this.teamData[data.teamId].allowedReceiveFromTeams.push(foundTeam.teamId);
      else
        console.log("Team not found");
    },
    async updateTeam(updatedTeam) {
      try {
        const response = await fetch('/api/update-team', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(updatedTeam),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }

      //If we have added a new authorized team
      const previousTeam = this.teamData[updatedTeam.teamId];
      if(updatedTeam.authorizedTeams)
      for(const [i,team] of updatedTeam.authorizedTeams.entries()){
        if(team == ""){
          if (updatedTeam.authorizedTeams[i] != ""){
            this.teamData[updatedTeam.teamId].authorizedTeams.splice(i,1,updatedTeam.authorizedTeams[i]);
          } else {
            this.teamData[updatedTeam.teamId].authorizedTeams.splice(i,1);
          }
        }
      }

      //If we have added a new user
      if(updatedTeam.userPermissions)
      for(const [i,user] of updatedTeam.userPermissions.entries()){
        if(user.userId == ""){
          if (updatedTeam.userPermissions[i] != ""){
            this.teamData[updatedTeam.teamId].userPermissions.splice(i,1,updatedTeam.userPermissions[i]);
          } else {
            this.teamData[updatedTeam.teamId].userPermissions.splice(i,1);
          }
        }
      }

      previousTeam.teamName = updatedTeam.teamName;
    },
    async addSubtask(subtask) {
      this.teamData[subtask.team].taskStorage.push(subtask);
      let parentTask = this.teamData[subtask.team].taskStorage.find(task=>task.taskId == subtask.parent);
      parentTask.subtasks.push(subtask.taskId);
      try {
        const response = await fetch('/api/add-subtask', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(subtask),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
    },
    async taskReview(task){
      try {
        const response = await fetch('/api/task-review', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(task),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
      let foundTask = this.teamData[task.team].taskStorage.find(taskF=>taskF.taskId == task.taskId)
      if(!foundTask)
        console.log("Task not found");
      foundTask.valuation = task.valuation;
      foundTask.status="Done"
      foundTask.type="Assigned"
    },
    async taskTransfer(data){
      const task = data.task;
      const team = data.team;
      try {
        const response = await fetch('/api/task-transfer', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
      let foundTask = this.teamData[task.team].taskStorage.find(taskF=>taskF.taskId == task.taskId)
      if(!foundTask)
        console.log("Task not found");

      foundTask.type = "Unassigned";
      foundTask.receivedFrom = task.team;
      foundTask.team = team;
    },
    async taskAccept(task){
      let foundTask = this.teamData[task.team].taskStorage.find(taskF => taskF.taskId == task.taskId);
      foundTask.status = "To Do";
      foundTask.type = "Assigned";
      foundTask.assignedTo = [this.loginUser.userId];

      try {
        const response = await fetch('/api/task-accept', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(task),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
    },
    async taskAssign(task){
      let foundTask = this.teamData[task.team].taskStorage.find(taskF => taskF.taskId == task.taskId);
      foundTask.assignedTo = task.assignedTo;
      foundTask.type = "Assigned";
      foundTask.status = "Pending Approval";

      try {
        const response = await fetch('/api/task-assign', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(task),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }

      if(task.parent)
        this.calculateProgress(task.parent,task.team);
    },
    async taskAuction(task){
      let foundTask = this.teamData[task.team].taskStorage.find(taskF => taskF.taskId == task.taskId);

      foundTask.type = "Auctioned";
      foundTask.auction = {};
      foundTask.status = "Pending Approval"
      foundTask.auction.deadline = task.auction.deadline;
      foundTask.auction.isAuctioned = false;
      foundTask.auction.bids = [];

      try {
        const response = await fetch('/api/task-auction', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(task),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }

    },
    async taskForfeit(task){
      try {
        const response = await fetch('/api/task-forfeit', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(task),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }

      let foundTaskIndex = this.teamData[task.team].taskStorage.findIndex(taskF => taskF.taskId == task.taskId);
      let foundTask = this.teamData[task.team].taskStorage[foundTaskIndex];
      if(foundTask.type == "Unassigned"){
        if(!foundTask.receivedFrom){
          this.teamData[foundTask.team].taskStorage.splice(foundTaskIndex,1);
        } else{
          foundTask.team = foundTask.receivedFrom;
          this.teamData[foundTask.team].taskStorage.splice(foundTaskIndex,1, foundTask);
        }
      }
      else{
        foundTask.type = "Unassigned";
        this.teamData[foundTask.team].taskStorage.splice(foundTaskIndex,1,foundTask);
      }
    },
    async taskAuctionUpdate(task){
      try {
        const response = await fetch('/api/task-auction-update', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(task),
        });

        if (!response.ok) {
          if(response?.status == 401)
            this.$emit("update:currentPage",'login');
          const error = await response.json();
          throw new Error(error.message);
        }
      } catch (error) {
        this.errorMessage = error.message || "Network error.";
        alert(this.errorMessage);
      }
      let foundTask = this.teamData[task.team].taskStorage.find(taskF => taskF.taskId == task.taskId);
      let minUser = null;
      let minBid = 9999;

      if(!foundTask){
        console.log("Task not found");
        return;
      }
      else if(!(foundTask.auction.bids && true) || !(foundTask.auction.bids.length && true)){
        console.log("No task bids");
        foundTask.type = "Unassigned";
        foundTask.status = "Pending Approval";
        return;
      }

      for(let bid of foundTask.auction.bids){
        if(minBid > bid.bidHours)
          minUser = bid.userId;
      }

      foundTask.assignedTo = [minUser]
      foundTask.type = "Assigned";
      foundTask.status = "Pending Approval";
      foundTask.auction.isAuctioned = true;
      return;
    },

  }
}
</script>
