<template>
  <v-container >
    <!-- Tasks to Review -->
    <v-row >
      <v-col cols="12">
        <v-list dense>
          <v-list-item
            v-for="task in reviewTasks"
            :key="task.taskId"
            class="task-item"
          >
            <!-- Task Summary -->
            <v-list-item-content @click="$emit('update:isEditingTask',task.taskId)">
              <v-list-item-title>
                <strong>{{ task.title }}</strong>
              </v-list-item-title>
              <v-list-item-subtitle>
                <strong>Deadline:</strong> {{ formatDate(task.deadline) }}
                <v-chip
                  :color="getPriorityColor(task.priority)"
                  class="priority-chip"
                  small
                >
                  {{ task.priority }}
                </v-chip>
              </v-list-item-subtitle>
                <!-- Star Rating -->
            </v-list-item-content>
            <v-btn
              icon
              @click="toggleTask(task.taskId)"
            >
              <v-icon>{{ isTaskExpanded(task.taskId) ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>

            <!-- Expanded Task Details -->
            <v-expand-transition>
              <div v-show="isTaskExpanded(task.taskId)" class="task-details">
                <p><strong>Description:</strong> {{ task.description }}</p>
                <p><strong>Estimated Hours:</strong> {{ task.estimatedHours }}</p>
                <p><strong>Created By:</strong> {{ getUserName(task.createdBy) }}</p>
                <p><strong>Value:</strong> ${{ task.valueGenerated }}</p>

                <!-- Comments Section -->
                <v-btn
                  text
                  small
                  color="primary"
                  @click="toggleComments(task.taskId)"
                >
                  {{ areCommentsExpanded(task.taskId) ? 'Hide Comments' : 'Show Comments' }}
                </v-btn>
                <v-expand-transition>
                  <div v-show="areCommentsExpanded(task.taskId)" class="comments-section">
                    <v-list dense>
                      <v-list-item
                        v-for="comment in task.comments"
                        :key="comment.commentId"
                      >
                        <v-list-item-content>
                          <p>
                            <strong>{{ getUserName(comment.author) }}</strong>:
                            {{ comment.content }}
                          </p>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list>
                  </div>
                </v-expand-transition>
                <div align="center" class="mt-3">
                  <span><strong>Rate Task:</strong></span>
                  <v-spacer></v-spacer>
                  <div class="stars">
                    <v-icon
                      v-for="index in 5"
                      :key="index"
                      :color="index <= task.rating ? 'yellow' : 'gray'"
                      @click="rateTask(task, index)"
                      class="star-icon"
                    >
                      mdi-star
                    </v-icon>
                    <v-text-field
                      label="Add Feedback"
                      v-model="task.valuation.feedback"
                    >
                    </v-text-field>

                  </div>
                </div>

              </div>
            </v-expand-transition>
          </v-list-item>
        </v-list>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: "TaskReview",
  props: {
    tasks: {
      type: Array,
      required: true,
    },
    users: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      expandedTasks: {}, // Tracks expanded tasks
      expandedComments: {}, // Tracks expanded comments for each task
    };
  },
  computed: {
    reviewTasks() {
      return this.tasks.filter((task) => task.type === "Review");
    },
  },
  methods: {
    getUserName(userId) {
      const user = this.users.find((u) => u.userId === userId);
      return user ? user.username : "Unknown User";
    },
    getPriorityColor(priority) {
      const colors = {
        High: "red",
        Medium: "orange",
        Low: "green",
        None: "gray",
      };
      return colors[priority] || "gray";
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString(undefined, {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
      });
    },
    toggleTask(taskId) {
      this.expandedTasks[taskId] = !this.expandedTasks[taskId];
    },
    isTaskExpanded(taskId) {
      return !!this.expandedTasks[taskId];
    },
    toggleComments(taskId) {
      this.expandedComments[taskId] = !this.expandedComments[taskId];
    },
    areCommentsExpanded(taskId) {
      return !!this.expandedComments[taskId];
    },
    rateTask(task, rating) {
      task.valuation.completionRating = rating; // Update task rating
      console.log(`Rated task ${task.taskId} with ${rating} stars`);
      this.$emit("update:networkRequests", 'task-review', task);
    },
  },
};
</script>
<style scoped>
.task-item {
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 10px;
}

.task-details {
  padding: 10px;
  background: #f9f9f9;
  border-radius: 5px;
}

.comments-section {
  padding: 10px;
  background: #f0f0f0;
  border-radius: 5px;
  margin-top: 10px;
}

.priority-chip {
  font-weight: bold;
  color: white;
}

.star-icon {
  cursor: pointer;
  font-size: 24px;
  margin-right: 4px;
}
</style>
